<template>
  <Loading :active="isLoading"></Loading>
  <div class="container">
    <div class="row mt-4">
      <!--  -->
      <div class="col-md-7">
        <table class="table align-middle">
          <thead>
          <tr>
            <th></th>
            <th></th>
            <th></th>
            <th></th>
          </tr>
          </thead>
          <tbody>
          <tr v-for="item in sortProducts" :key="item.id">
            <td style="width: 200px">
              <div style="height: 100px; background-size: cover; background-position: center"
                  :style="{backgroundImage: `url(${item.imageUrl})`}"></div>
            </td>
            <td>{{ item.title }}</td>
            <td>
              <div class="h5"> {{ item.origin_price }} </div>
            </td>
            <td>
              <div class="btn-group btn-group-sm text-nowrap">
                <button type="button" class="btn btn-outline-danger p-2"
                        @click="addToCart(item.id)"
                        :disabled="loadingItem === item.id">
                  <span class="spinner-border spinner-grow-sm"
                      v-if="loadingItem === item.id"></span>
                  
                </button>
              </div>
            </td>
          </tr>
          </tbody>
        </table>
      </div>
      <!--  -->
      <div class="col-md-5">
        <div class="sticky-top">
          <table class="table align-middle">
            <thead>
              <tr>
                <th></th>
                <th></th>
                <th style="width: 130px"></th>
                <th></th>
              </tr>
            </thead>
            <tbody>
            <template v-if="cart.carts">
              <tr v-for="item in cart.carts" :key="item.id">
                <td>
                  <button type="button" class="btn btn-outline-danger btn-sm"
                          :disabled="loadingItem === item.id"
                          @click="removeCartItem(item.id)">
                    x
                  </button>
                </td>
                <td>
                  {{ item.product.title }}
                  <div class="text-success" v-if="item.coupon">
                    
                  </div>
                </td>
                <td>
                  <div class="input-group input-group-sm">
                    <input type="number" class="form-control"
                          v-model.number="item.qty"
                          :disabled="loadingItem === item.id"
                          @change="updateCart(item)">
                    <div class="input-group-text">/ {{ item.product.unit }}</div>
                  </div>
                </td>
                <td class="text-end">
                  <small v-if="cart.final_total !== cart.total" class="text-success"></small>
                  {{ $filters.currency(item.final_total) }}
                </td>
              </tr>
            </template>
            </tbody>
            <tfoot>
            <tr>
              <td colspan="3" class="text-end"></td>
              <td class="text-end">{{ $filters.currency(cart.total) }}</td>
            </tr>
            <tr v-if="cart.final_total !== cart.total">
              <td colspan="3" class="text-end text-success"></td>
              <td class="text-end text-success">{{ $filters.currency(cart.final_total) }}</td>
            </tr>
            </tfoot>
          </table>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { mapState, mapActions } from 'pinia';
import productsStore from '@/stores/productStore';
import cartStore from '@/stores/cartStore';
import statusStore from '@/stores/statusStore';

export default {
  computed: {
    ...mapState(productsStore, ['sortProducts']),
    ...mapState(cartStore, ['cart']),
    ...mapState(statusStore, ['isLoading', 'loadingItem']),
  },
  methods: {
    ...mapActions(productsStore, ['getProducts']),
    ...mapActions(cartStore, ['addToCart', 'getCart', 'updateCart', 'removeCartItem']),
  },
  created() {
    this.getProducts();
    this.getCart();
  },
};
</script>
